import {FormGroup} from '@angular/forms';
import {RequestStatus} from '@app/modules/newscenter/RequestStatus.model';
import {Logger} from '@upe/logger';
import {BehaviorSubject, Observable} from 'rxjs';

export abstract class BaseConfigComponent<T>{

    protected _initialStatus$: BehaviorSubject<RequestStatus>;
    protected _submitStatus$: BehaviorSubject<RequestStatus>;

    abstract formLayout:any;
    protected abstract _form: FormGroup;
    protected _logger:Logger;
    protected abstract _mappedModel$: BehaviorSubject<T>;

    constructor(name: string) {
        this._initialStatus$ =  new BehaviorSubject<RequestStatus>({status: 'idle'})
        this._submitStatus$ =  new BehaviorSubject<RequestStatus>({status: 'idle'})
        this._logger = new Logger({ name: name , flags: ["component"] });
    }
    protected get form(){
        return this._form.controls
    }

    onSubmit():void{
        this._logger.debug('Initializing submit')
        this._submitStatus$.next({status: 'pending'})
        this._submitData()
    }
    protected abstract _submitData():void


}
